import Noticia from './Noticia'

const Noticias = (props) => {

console.log(props.noticias.articles);    
    return ( 
        <div className="row">
            {props.noticias.map(noticia => (
                <Noticia 
                    noticia={noticia}
                />    
            ))}
        </div>
     );
}
 
export default Noticias;